import React, {Component} from "react";
import {connect} from "react-redux";
import PropTypes from "prop-types";
import {Expander, Section, Trigger} from "eshop/modules/core/components/ui/Expander";
import {getCartSummaryTableDetailsRows, getCartDevices, getOfferDescriptionForBundles, getOfferCount, getMaxBundleNo, getDepositCost, getCartCheckoutCost} from "../selectors";
import {getSelectedDeliveryMethod} from "eshop/modules/checkout/selectors";
import SummaryDetailsTableForBundles from "./SummaryDetailsTableForBundles";
import OnlineUtils from "eshop/utils/OnlineUtils";
import {getFeeForSelectedPaymentMethod} from "../../checkout/selectors";
import DeliveryCostDetails from "./DeliveryCostDetails";

class SummaryDetailsForBundles extends Component {

    constructor(props) {
        super(props);
    }

    componentDidMount() {
        this.loadModule();
    }

    titleView(bundleNo){
        const description = this.props.description.filter(desc => desc[0] === bundleNo);
        const sectionHeader =
            [<h4 key={"title-bundle" + bundleNo} className="h5">{description[0][2]}</h4>,<p key={"description-bundle" + bundleNo}>{description[0][1]}</p>,
            <Trigger key={"trigger-bundle" + bundleNo} triggerClassName="opl-document__expander__trigger"><span className="opl-section__text-show">rozwiń opłaty</span>
            <span className="opl-section__text-hide">zwiń opłaty</span></Trigger>]
        return sectionHeader;
    }

    entryNotNull(entry) {
        return entry.monthlyPrice || entry.oneTimePrice || entry.monthTo;
    }

    getCheckoutCurrency() {
        return !!this.props.checkoutCost ? this.props.checkoutCost.currency : "zł";
    }

    render() {
        let rows = [];
        let row;
        let expanderRow;
        let entriesForBundle = [];
        for (var bundleNo = 1; bundleNo <= this.props.maxBundleNumber ; bundleNo++){
        entriesForBundle = this.props.entries.filter(entryForBundle => entryForBundle.bundleNo === bundleNo).filter(entry => this.entryNotNull(entry));
            if (entriesForBundle.length > 0){
                row = entriesForBundle.map((e, i) =>
                      <SummaryDetailsTableForBundles key={i + "bundleNo" + bundleNo} entry={e} monthlyHeader={this.props.monthlyHeader}
                          oneTimeHeader={this.props.oneTimeHeader} id={i ? (this.props.id + "-" + i + "-bundle" + bundleNo) : (this.props.id + "-bundle" + bundleNo)} bundleNo={bundleNo}
                          className={(i > 0) ? "u-padding-left-l-xl " : " u-padding-left-l-xl "}
                          checkoutPriceTooltipContent={this.props.checkoutPriceTooltipContent}
                          firstMonthPriceTooltipContent={this.props.firstMonthPriceTooltipContent}/>
                );
            expanderRow =
                <Expander key={"bundleNo-" + bundleNo + "-expander"} variant="section_expander" scrollToSelected={false}>
                    <Section key={"bundleNo-" + bundleNo + "-section"} header={this.titleView(bundleNo)} headerClassName="u-small-padding" variant="section_expander"
                    className="opl-document__expander opl-section--chevron-left u-no-padding-top" contentClassName="opl-document__expander__content">
                        <table id="ExpanderSummaryDetailsForBundleTable" key={ "bundleNo-" + bundleNo + "-table"} aria-hidden="true" className="u-no-margin-t opl-table opl-conf-summary-table opl-table--50-50 u-small-hide u-no-border">
                            <caption/>
                            <thead>
                              <tr>
                                <th/>
                                <th/>
                                <th className="second-last u-font-normal u-padding-right">{this.props.oneTimeHeader}</th>
                                <th className="last u-font-normal u-no-padding-right">{this.props.monthlyHeader}</th>
                              </tr>
                            </thead>
                          </table>
                        <Expander key={"bundleNo-" + bundleNo + "-expander-nested"}
                            variant="section_expander"
                            scrollToSelected={false}>
                            {row}
                        </Expander>
                    </Section>
                </Expander>;

            rows.push(expanderRow);
            }
        }
        return (
            <div data-js-module="core/modules/expander" id={this.props.rootComponent + "-details"}>
                <div className="js-expander__container u-margin-top">
                    <a href="#" className="js-expander__trigger u-padding-left-s u-small-no-padding">
                        <span className="js-expander__trigger--hide">{this.props.foldedSectionTriggerLabel}</span>
                        <span className="js-expander__trigger--show">{this.props.expandedSectionTriggerLabel}</span>
                    </a>
                    <div className="js-expander__content u-hide--soft">
                        <h3 className="u-font-standard u-padding-top-l u-padding-l u-no-margin opl-section__heading">
                            <span className="h2 u-no-margin">Szczegóły opłat</span>
                        </h3>
                        {rows}
                        {this.props.deliveryMethod &&
                        <DeliveryCostDetails
                            title={"Dostawa"}
                            cost={this.props.deliveryMethod.price.replace(".", ",")}
                            render={(oneTimePrice, monthlyPrice, title) => this.prepareCustomSummaryRow(title, oneTimePrice, monthlyPrice)}/>
                        }
                        {this.props.feeForSelectedPaymentMethod && parseInt(this.props.feeForSelectedPaymentMethod) !== 0 &&
                        <DeliveryCostDetails
                            title={"Opłata za płatność przy odbiorze"}
                            cost={this.props.feeForSelectedPaymentMethod.replace(".", ",")}
                            render={(oneTimePrice, monthlyPrice, title) => this.prepareCustomSummaryRow(title, oneTimePrice, monthlyPrice)}/>
                        }
                        {this.props.depositCost > 0 ? this.depositTableView() : null}
                        <div className="u-margin-top">
                            <a href="#" className="js-expander__trigger u-padding-left-s u-small-no-padding">
                                <span
                                    className="js-expander__trigger--hide">{this.props.foldedSectionTriggerLabel}</span>
                                <span
                                    className="js-expander__trigger--show">{this.props.expandedSectionTriggerLabel}</span>
                            </a>
                        </div>
                    </div>
                </div>
            </div>
        );
    }

    prepareCustomSummaryRow(title, oneTimePrice, monthlyPrice) {
        return (
            <div className="l-row">
                <div className="l-col l-col-12">
                    <table className="u-spacing-top-l opl-table opl-conf-summary-table opl-table--50-50 opl-table--no-border-top">
                        <caption className="u-acc-hide">{title}</caption>
                        <thead className="u-acc-hide">
                            <tr>
                                <th/>
                                <th/>
                                <th>{this.props.oneTimeHeader}</th>
                                <th>{this.props.monthlyHeader}</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr className="u-small-no-padding">
                                <td className="u-w-auto">
                                <h3 className="h5 g-brand2-c u-no-spacing">{title}</h3>
                                </td>
                                <td data-title={this.props.oneTimeHeader} className="second-last u-small-padding-l g-brand2-c">
                                    {oneTimePrice}
                                </td>
                                <td data-title={this.props.monthlyHeader} className="last g-brand2-c td-null">
                                    {monthlyPrice}
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </div>
            </div>
        );
    }

    depositTableView() {
        const monthlyPrice = <span>&mdash;</span>;
        const oneTimePrice = <span className="h5 g-brand2-c u-no-margin">{OnlineUtils.formatPrice(this.props.depositCost) + " " + this.getCheckoutCurrency()}</span>;

        return this.prepareCustomSummaryRow("Kaucje", oneTimePrice, monthlyPrice);
    }

    loadModule() {
        OPL.UI.loadModules(document.getElementById(this.props.rootComponent + "-details"), [{
            path: "core/modules/expander",
            options: "",
            conditions: "",
        }]);
    }

}

SummaryDetailsForBundles.propTypes = {
    rootComponent: PropTypes.string,
    expandedSectionTriggerLabel: PropTypes.string,
    foldedSectionTriggerLabel: PropTypes.string,
    feeForSelectedPaymentMethod: PropTypes.string,
    deliveryMethod: PropTypes.any,
};

SummaryDetailsForBundles.defaultProps = {
    expandedSectionTriggerLabel: "Ukryj szczegóły opłat",
    foldedSectionTriggerLabel: "Pokaż szczegóły opłat",
};

const mapStateToProps = state => ({
    entries: getCartSummaryTableDetailsRows(state),
    description: getOfferDescriptionForBundles(state),
    devices: getCartDevices(state) || [],
    deliveryMethod: getSelectedDeliveryMethod(state),
    offerCount: getOfferCount(state),
    maxBundleNumber: getMaxBundleNo(state),
    depositCost: getDepositCost(state),
    checkoutCost: getCartCheckoutCost(state),
    feeForSelectedPaymentMethod: getFeeForSelectedPaymentMethod(state),
});

const mapDispatchToProps = dispatch => ({

});

export default connect(mapStateToProps, mapDispatchToProps)(SummaryDetailsForBundles);
